.App {
  text-align: center;
}

.container {
  display: flex;
  flex-direction: column;
}

.flexContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.infoPanel {  
  margin: 0 auto;
}

.smallMargin {
  margin: 5px;
}


.flexContainerNoCenter {
  display: flex;
  align-items: center;
  gap: 10px;
}

.row1 {
  display: flex;
  flex-direction: row;
  grid-template-columns: 50%, 50%;
  border: 2px solid darkslategray;
  border-radius: 2px;
  height:400px;
}

.Tasks, .Inventory {
  height: 100%;
  width: 50%;
  border: 1px solid grey;
  z-index: 10;
}


.taskContainer img {
  max-height: 20px;
}

.Events {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 2px solid darkslategray;
  padding: 8px;
}
